import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { FooterPagesComponent } from './footer-pages.component';

const routes: Routes = [
    {
        path: '',
        component: FooterPagesComponent,
        children: [
            {
                path: 'about',
                loadChildren: () => import('./about/about.module').then((m) => m.AboutModule),
            },
            {
                path: 'contacts',
                loadChildren: () => import('./contacts/contacts.module').then((m) => m.ContactsModule),
            },
            {
                path: 'help',
                loadChildren: () => import('./help/help.module').then((m) => m.HelpModule),
            },
            {
                path: 'terms',
                loadChildren: () =>
                    import('./terms-and-conditions/terms-and-conditions.module').then(
                        (m) => m.TermsAndConditionsModule
                    ),
            },
            {
                path: 'ppc',
                loadChildren: () => import('./privacy-policy/privacy-policy.module').then((m) => m.PrivacyPolicyModule),
            },
            {
                path: '',
                pathMatch: 'full',
                redirectTo: 'about',
            },
        ],
    },
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule],
})
export class FooterPagesRoutingModule {}
